/**
 * AddFoodModal - Modal for adding new food listings
 * Allows food providers to add their surplus food items
 */
import React, { useState } from 'react';
import { X, Plus, Clock, MapPin, DollarSign, Leaf, Tag, Camera, Image as ImageIcon } from 'lucide-react';
import { useToast } from '../../contexts/ToastContext';
import foodService from '../../services/foodService';
import ImageUpload from './ImageUpload';

const AddFoodModal = ({ isOpen, onClose, onFoodAdded }) => {
  const [formData, setFormData] = useState({
    name: '',
    restaurant_name: '',
    description: '',
    original_price: '',
    discounted_price: '',
    quantity: '',
    pickup_date: '',
    pickup_window_start: '',
    pickup_window_end: '',
    location: '',
    dietary_info: [],
    image_emoji: '',
    co2_saved: '0'
  });
  
  const [isLoading, setIsLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [uploadedImages, setUploadedImages] = useState([]);
  const [createdFoodId, setCreatedFoodId] = useState(null);
  const [useImageUpload, setUseImageUpload] = useState(false);
  const [imageFiles, setImageFiles] = useState([]);
  const toast = useToast();

  // Dietary options
  const dietaryOptions = [
    'Halal', 'Vegetarian', 'Vegan', 'Gluten-Free', 'Dairy-Free', 
    'Nut-Free', 'Organic', 'Contains Gluten', 'Vegetarian Options'
  ];

  // Food emoji options
  const emojiOptions = [
    '', '', '', '', '', '', '', '', '', '',
    '', '', '', '', '', '', '', '', '', ''
  ];

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const handleDietaryChange = (dietary) => {
    setFormData(prev => ({
      ...prev,
      dietary_info: prev.dietary_info.includes(dietary)
        ? prev.dietary_info.filter(d => d !== dietary)
        : [...prev.dietary_info, dietary]
    }));
  };

  const validateForm = () => {
    const newErrors = {};
    
    if (!formData.name.trim()) newErrors.name = 'Food name is required';
    if (!formData.restaurant_name.trim()) newErrors.restaurant_name = 'Restaurant name is required';
    if (!formData.description.trim()) newErrors.description = 'Description is required';
    if (!formData.original_price || parseFloat(formData.original_price) <= 0) {
      newErrors.original_price = 'Original price must be greater than 0';
    }
    if (!formData.discounted_price || parseFloat(formData.discounted_price) < 0) {
      newErrors.discounted_price = 'Discounted price cannot be negative';
    }
    if (parseFloat(formData.discounted_price) > parseFloat(formData.original_price)) {
      newErrors.discounted_price = 'Discounted price cannot be higher than original price';
    }
    if (!formData.quantity || parseInt(formData.quantity) <= 0) {
      newErrors.quantity = 'Quantity must be at least 1';
    }
    if (!formData.pickup_date) newErrors.pickup_date = 'Pickup date is required';
    if (!formData.pickup_window_start) newErrors.pickup_window_start = 'Pickup start time is required';
    if (!formData.pickup_window_end) newErrors.pickup_window_end = 'Pickup end time is required';
    if (!formData.location.trim()) newErrors.location = 'Location is required';
    
    // Validate pickup times
    if (formData.pickup_window_start && formData.pickup_window_end) {
      if (formData.pickup_window_start >= formData.pickup_window_end) {
        newErrors.pickup_window_end = 'End time must be after start time';
      }
    }
    
    // Validate pickup date is not in the past
    if (formData.pickup_date) {
      const pickupDate = new Date(formData.pickup_date);
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      if (pickupDate < today) {
        newErrors.pickup_date = 'Pickup date cannot be in the past';
      }
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleImageUpload = (image) => {
    setUploadedImages(prev => [...prev, image]);
  };

  const handleImageDelete = (imageId) => {
    setUploadedImages(prev => prev.filter(img => img.id !== imageId));
  };

  const handleImageSelect = (e) => {
    const files = Array.from(e.target.files);
    
    // Validate file count
    if (imageFiles.length + files.length > 5) {
      alert('You can only upload up to 5 images.');
      return;
    }

    // Validate file types and sizes
    const validFiles = files.filter(file => {
      if (!file.type.startsWith('image/')) {
        alert(`${file.name} is not an image file.`);
        return false;
      }
      if (file.size > 5 * 1024 * 1024) { // 5MB limit
        alert(`${file.name} is too large. Please choose a file smaller than 5MB.`);
        return false;
      }
      return true;
    });

    setImageFiles(prev => [...prev, ...validFiles]);
  };

  const removeImage = (index) => {
    setImageFiles(prev => prev.filter((_, i) => i !== index));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      toast.error('Please fix the errors in the form');
      return;
    }

    setIsLoading(true);
    
    try {
      const foodData = {
        name: formData.name,
        restaurant_name: formData.restaurant_name,
        description: formData.description,
        original_price: parseFloat(formData.original_price),
        discounted_price: parseFloat(formData.discounted_price),
        quantity: parseInt(formData.quantity),
        pickup_date: formData.pickup_date,
        pickup_window_start: formData.pickup_window_start,
        pickup_window_end: formData.pickup_window_end,
        location: formData.location,
        dietary_info: formData.dietary_info,
        image_emoji: formData.image_emoji,
        co2_saved: parseFloat(formData.co2_saved)
      };

      const newFood = await foodService.createFoodListing(foodData);
      setCreatedFoodId(newFood.id);
      
      // Upload images if any were selected
      if (imageFiles && imageFiles.length > 0) {
        try {
          for (const file of imageFiles) {
            const formData = new FormData();
            formData.append('image', file);
            formData.append('alt_text', file.name);
            formData.append('is_primary', imageFiles.indexOf(file) === 0); // First image is primary

            await fetch(
              `http://localhost:8000/api/foods/images/${newFood.id}/upload/`,
              {
                method: 'POST',
                headers: {
                  'Authorization': `Bearer ${localStorage.getItem('kindbite_access_token')}`,
                },
                body: formData,
              }
            );
          }
          toast.success('Food item and images uploaded successfully!');
        } catch (imageError) {
          console.error('Error uploading images:', imageError);
          toast.success('Food item added successfully, but there was an error uploading images.');
        }
      } else {
        toast.success('Food item added successfully!');
      }
      
      onFoodAdded(newFood, imageFiles);
      handleClose();
      
    } catch (error) {
      console.error('Error adding food:', error);
      toast.error(error.message || 'Failed to add food item. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  const handleClose = () => {
    setFormData({
      name: '',
      restaurant_name: '',
      description: '',
      original_price: '',
      discounted_price: '',
      quantity: '',
      pickup_date: '',
      pickup_window_start: '',
      pickup_window_end: '',
      location: '',
      dietary_info: [],
      image_emoji: '',
      co2_saved: '0'
    });
    setErrors({});
    setUploadedImages([]);
    setCreatedFoodId(null);
    setUseImageUpload(false);
    setImageFiles([]);
    onClose();
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b">
          <div className="flex items-center space-x-2">
            <Plus className="w-6 h-6 text-green-600" />
            <h2 className="text-xl font-semibold text-gray-800">Add Food Item</h2>
          </div>
          <button
            onClick={handleClose}
            className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
          >
            <X className="w-5 h-5" />
          </button>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="p-6 space-y-6">
          {/* Basic Information */}
          <div className="space-y-4">
            <h3 className="text-lg font-medium text-gray-800 flex items-center space-x-2">
              <Tag className="w-5 h-5" />
              <span>Basic Information</span>
            </h3>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Food Name *
                </label>
                <input
                  type="text"
                  name="name"
                  value={formData.name}
                  onChange={handleInputChange}
                  className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 ${
                    errors.name ? 'border-red-500' : 'border-gray-300'
                  }`}
                  placeholder="e.g., Mixed Rice & Chicken"
                />
                {errors.name && <p className="text-red-500 text-xs mt-1">{errors.name}</p>}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Restaurant/Business Name *
                </label>
                <input
                  type="text"
                  name="restaurant_name"
                  value={formData.restaurant_name}
                  onChange={handleInputChange}
                  className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 ${
                    errors.restaurant_name ? 'border-red-500' : 'border-gray-300'
                  }`}
                  placeholder="e.g., Mama's Kitchen"
                />
                {errors.restaurant_name && <p className="text-red-500 text-xs mt-1">{errors.restaurant_name}</p>}
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Description *
              </label>
              <textarea
                name="description"
                value={formData.description}
                onChange={handleInputChange}
                rows={3}
                className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 ${
                  errors.description ? 'border-red-500' : 'border-gray-300'
                }`}
                placeholder="Describe the food item..."
              />
              {errors.description && <p className="text-red-500 text-xs mt-1">{errors.description}</p>}
            </div>
          </div>

          {/* Pricing */}
          <div className="space-y-4">
            <h3 className="text-lg font-medium text-gray-800 flex items-center space-x-2">
              <DollarSign className="w-5 h-5" />
              <span>Pricing</span>
            </h3>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Original Price (UGX) *
                </label>
                <input
                  type="number"
                  name="original_price"
                  value={formData.original_price}
                  onChange={handleInputChange}
                  min="0"
                  step="100"
                  className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 ${
                    errors.original_price ? 'border-red-500' : 'border-gray-300'
                  }`}
                  placeholder="15000"
                />
                {errors.original_price && <p className="text-red-500 text-xs mt-1">{errors.original_price}</p>}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Discounted Price (UGX) *
                </label>
                <input
                  type="number"
                  name="discounted_price"
                  value={formData.discounted_price}
                  onChange={handleInputChange}
                  min="0"
                  step="100"
                  className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 ${
                    errors.discounted_price ? 'border-red-500' : 'border-gray-300'
                  }`}
                  placeholder="5000"
                />
                {errors.discounted_price && <p className="text-red-500 text-xs mt-1">{errors.discounted_price}</p>}
              </div>
            </div>
          </div>

          {/* Availability */}
          <div className="space-y-4">
            <h3 className="text-lg font-medium text-gray-800 flex items-center space-x-2">
              <Clock className="w-5 h-5" />
              <span>Availability</span>
            </h3>
            
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Quantity *
                </label>
                <input
                  type="number"
                  name="quantity"
                  value={formData.quantity}
                  onChange={handleInputChange}
                  min="1"
                  className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 ${
                    errors.quantity ? 'border-red-500' : 'border-gray-300'
                  }`}
                  placeholder="8"
                />
                {errors.quantity && <p className="text-red-500 text-xs mt-1">{errors.quantity}</p>}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Pickup Date *
                </label>
                <input
                  type="date"
                  name="pickup_date"
                  value={formData.pickup_date}
                  onChange={handleInputChange}
                  min={new Date().toISOString().split('T')[0]}
                  className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 ${
                    errors.pickup_date ? 'border-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.pickup_date && <p className="text-red-500 text-xs mt-1">{errors.pickup_date}</p>}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  CO2 Saved (kg)
                </label>
                <input
                  type="number"
                  name="co2_saved"
                  value={formData.co2_saved}
                  onChange={handleInputChange}
                  min="0"
                  step="0.1"
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500"
                  placeholder="2.4"
                />
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Pickup Start Time *
                </label>
                <input
                  type="time"
                  name="pickup_window_start"
                  value={formData.pickup_window_start}
                  onChange={handleInputChange}
                  className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 ${
                    errors.pickup_window_start ? 'border-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.pickup_window_start && <p className="text-red-500 text-xs mt-1">{errors.pickup_window_start}</p>}
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Pickup End Time *
                </label>
                <input
                  type="time"
                  name="pickup_window_end"
                  value={formData.pickup_window_end}
                  onChange={handleInputChange}
                  className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 ${
                    errors.pickup_window_end ? 'border-red-500' : 'border-gray-300'
                  }`}
                />
                {errors.pickup_window_end && <p className="text-red-500 text-xs mt-1">{errors.pickup_window_end}</p>}
              </div>
            </div>
          </div>

          {/* Location */}
          <div className="space-y-4">
            <h3 className="text-lg font-medium text-gray-800 flex items-center space-x-2">
              <MapPin className="w-5 h-5" />
              <span>Location</span>
            </h3>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Pickup Location *
              </label>
              <input
                type="text"
                name="location"
                value={formData.location}
                onChange={handleInputChange}
                className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 ${
                  errors.location ? 'border-red-500' : 'border-gray-300'
                }`}
                placeholder="e.g., Nakasero, Kampala"
              />
              {errors.location && <p className="text-red-500 text-xs mt-1">{errors.location}</p>}
            </div>
          </div>

          {/* Dietary Information */}
          <div className="space-y-4">
            <h3 className="text-lg font-medium text-gray-800 flex items-center space-x-2">
              <Leaf className="w-5 h-5" />
              <span>Dietary Information</span>
            </h3>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Select applicable dietary options
              </label>
              <div className="grid grid-cols-2 md:grid-cols-3 gap-2">
                {dietaryOptions.map((option) => (
                  <label key={option} className="flex items-center space-x-2 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={formData.dietary_info.includes(option)}
                      onChange={() => handleDietaryChange(option)}
                      className="w-4 h-4 text-green-600 border-gray-300 rounded focus:ring-green-500"
                    />
                    <span className="text-sm text-gray-700">{option}</span>
                  </label>
                ))}
              </div>
            </div>
          </div>

          {/* Food Image/Emoji Selection */}
          <div className="space-y-4">
            <h3 className="text-lg font-medium text-gray-800">Food Image</h3>
            
            {/* Image Type Toggle */}
            <div className="flex space-x-2 mb-3">
              <button
                type="button"
                onClick={() => setUseImageUpload(false)}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
                  !useImageUpload 
                    ? 'bg-green-500 text-white' 
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                <ImageIcon className="w-4 h-4 inline mr-2" />
                Emoji
              </button>
              <button
                type="button"
                onClick={() => setUseImageUpload(true)}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
                  useImageUpload 
                    ? 'bg-green-500 text-white' 
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                <Camera className="w-4 h-4 inline mr-2" />
                Upload Image
              </button>
            </div>

            {!useImageUpload ? (
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Choose an emoji for your food item
                </label>
                <div className="grid grid-cols-10 gap-2">
                  {emojiOptions.map((emoji) => (
                    <button
                      key={emoji}
                      type="button"
                      onClick={() => setFormData(prev => ({ ...prev, image_emoji: emoji }))}
                      className={`w-10 h-10 text-2xl border-2 rounded-lg flex items-center justify-center transition-colors ${
                        formData.image_emoji === emoji
                          ? 'border-green-500 bg-green-50'
                          : 'border-gray-300 hover:border-gray-400'
                      }`}
                    >
                      {emoji}
                    </button>
                  ))}
                </div>
              </div>
            ) : (
              <div className="space-y-4">
                <div className="text-center text-gray-600 mb-4">
                  <Camera className="w-8 h-8 mx-auto mb-2 text-green-500" />
                  <p className="text-sm font-medium">Upload Food Images</p>
                  <p className="text-xs text-gray-500">Select up to 5 images (5MB each)</p>
                </div>
                
                {/* Image Upload Area */}
                <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center hover:border-green-400 transition-colors">
                  <input
                    type="file"
                    multiple
                    accept="image/*"
                    onChange={handleImageSelect}
                    className="hidden"
                    id="image-upload"
                  />
                  <label
                    htmlFor="image-upload"
                    className="cursor-pointer block"
                  >
                    <div className="space-y-2">
                      <div className="mx-auto w-12 h-12 bg-gray-100 rounded-full flex items-center justify-center">
                        <ImageIcon className="w-6 h-6 text-gray-400" />
                      </div>
                      <div>
                        <p className="text-sm font-medium text-gray-700">Click to select images</p>
                        <p className="text-xs text-gray-500">or drag and drop images here</p>
                      </div>
                    </div>
                  </label>
                </div>

                {/* Selected Images Preview */}
                {imageFiles.length > 0 && (
                  <div className="space-y-2">
                    <p className="text-sm font-medium text-gray-700">Selected Images ({imageFiles.length}/5):</p>
                    <div className="grid grid-cols-2 md:grid-cols-3 gap-2">
                      {imageFiles.map((file, index) => (
                        <div key={index} className="relative group">
                          <img
                            src={URL.createObjectURL(file)}
                            alt={`Preview ${index + 1}`}
                            className="w-full h-24 object-cover rounded-lg border border-gray-200"
                          />
                          <button
                            type="button"
                            onClick={() => removeImage(index)}
                            className="absolute top-1 right-1 bg-red-500 text-white p-1 rounded-full opacity-0 group-hover:opacity-100 transition-opacity"
                          >
                            <X size={12} />
                          </button>
                        </div>
                      ))}
                    </div>
                  </div>
                )}
              </div>
            )}
          </div>


          {/* Action Buttons */}
          <div className="flex justify-end space-x-3 pt-6 border-t">
            <button
              type="button"
              onClick={handleClose}
              className="px-6 py-2 text-gray-600 bg-gray-100 rounded-lg hover:bg-gray-200 transition-colors"
            >
              Cancel
            </button>
            <button
              type="submit"
              disabled={isLoading}
              className="px-6 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center space-x-2"
            >
              {isLoading ? (
                <>
                  <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                  <span>Adding...</span>
                </>
              ) : (
                <>
                  <Plus className="w-4 h-4" />
                  <span>Add Food Item</span>
                </>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default AddFoodModal;








